p {
  font-family: "Lato", "serif";
  color: #423F3F;
  line-height: 1.5em; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif;
  color: #423F3F; }

h2 {
  font-weight: 400;
  font-style: italic; }

.design-services {
  text-align: center;
  font-size: 1.4641em;
  font-style: normal !important; }
  @media (min-width: 15em) and (max-width: 60em) {
    .design-services {
      font-size: calc( 1.4641em + 1.69323 * ( ( 100vw - 15em) / 45 )); } }
  @media (min-width: 60em) {
    .design-services {
