.card-section {
  text-align: center; }

.link {
  text-decoration: none; }

.tag {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  display: block; }

.prj-head {
  text-align: center;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  margin: 0px; }
  .prj-head .prj-info {
    padding: 20px 10px 20px; }

.prj-head {
  font-size: 1.1em; }
  @media (min-width: 15em) and (max-width: 60em) {
    .prj-head {
      font-size: calc( 1.1em + 0.233 * ( ( 100vw - 15em) / 45 )); } }
  @media (min-width: 60em) {
