.info {
  font-size: 1em; }
  @media (min-width: 15em) and (max-width: 60em) {
    .info {
      font-size: calc( 1em + 0 * ( ( 100vw - 15em) / 45 )); } }
  @media (min-width: 60em) {
    .info {
      font-size: 1em; } }

.info {
  text-align: center;
  background-color: #4169e1;
  color: #f6f6f6;
  opacity: 0.8;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  padding: 2px; }

.p-card {
  margin: 10px;
  transition: box-shadow 0.4s;
  box-shadow: 0px 10px 10px 0px transparent; }

.view-icon {
  opacity: 0;
  transition: opacity 0.3s; }

.fullRect {
  opacity: 0;
  transition: opacity 1s; }

.view {
  fill: whitesmoke;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  font-size: 20px; }

